// –ë–ª–æ–∫–∏—Ä—É–µ–º –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—É—é –æ—Ä–∏–µ–Ω—Ç–∞—Ü–∏—é –Ω–∞ —Ç–µ–ª–µ—Ñ–æ–Ω–µ (landscape)
@media (orientation: landscape) and (max-height: 520px) {
  html, body {
    height: 100%;
    overflow: hidden !important; }

  body {
    position: relative;

    &::before {
      content: "–ü–æ–≤–µ—Ä–Ω–∏—Ç–µ —Ç–µ–ª–µ—Ñ–æ–Ω –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ";
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 99999;
      background: #CBFFB5;  // –º–æ–∂–µ—à—å –∑–∞–º–µ–Ω–∏—Ç—å –Ω–∞ $main-color
      color: #2F7B3E;       // –º–æ–∂–µ—à—å –∑–∞–º–µ–Ω–∏—Ç—å –Ω–∞ $text-color-light
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      padding: 24px;
      font-size: 18px;
      font-weight: 800; }

    &::after {
      content: "";
      position: fixed;
      left: 50%;
      top: 50%;
      transform: translate(-50%, 28px);
      width: 56px;
      height: 112px;
      border-radius: 14px;
      border: 2px solid rgba(47, 123, 62, .55);
      z-index: 100000; } } }


